import api from '../../api/api.js';
export default {
	props: {

	},
	data() {
		return {
		
			// title_img: require('../../static/imgs/info/title.png'),
			title_img: require('../../static/info/title.png'),
			bg_img: require('../../static/info/bg_img.png'),
			form_data: [{
				title: '姓名',
				placeholder: '张三',
				type: 'name',
				value: '',
				maxlength: 5,
				input_type: 'text',
				confirm_type: 'next',
			}, {
				title: '手机号',
				placeholder: '请输入手机号',
				type: 'mobile',
				value: '',
				maxlength: 11,

				input_type: 'Number',
				confirm_type: 'next',
			}, {
				title: '公司名称',
				placeholder: '请输入您的公司',
				type: 'companyName',
				value: '',
				maxlength: 20,
				input_type: 'text',
				confirm_type: 'next',
			}, {
				title: '你的职位',
				placeholder: '请输入你的职位',
				type: 'job',
				value: '',
				maxlength: 10,
				input_type: 'text',
				confirm_type: 'next',
			}, {
				title: '推荐人',
				placeholder: "请填写推荐人姓名",
				type: 'pname',
				value: '',
				maxlength: 5,
				input_type: 'text',
				confirm_type: 'done',
			}]
		}
	},
	methods: {
		show_info() {
			console.log(1)
			this.$root.subscribe()
		},
		formSubmit(e) {
			var reg=/^(((13[0-9]{1})|(15[0-9]{1})|(16[0-9]{1})|(17[3-8]{1})|(18[0-9]{1})|(19[0-9]{1})|(14[5-7]{1}))+\d{8})$/
			console.log(e.detail.value)
			for (let i = 0; i < this.form_data.length; i++) {
				if (this.form_data[i].value != '') {
					if (i == this.form_data.length - 1) {
						
						if (!reg.test(e.detail.value.mobile)) {
							uni.showToast({
								title: '手机号格式有误',
								duration: 2000,
								icon: 'none',
							});

						} else {
							api.meeting(e.detail.value, function(res) {
								console.log(res)
								if (res.error == 0) {
									this.$root.subscribe()
									uni.showToast({
										title: res.message,
										duration: 2000,
									
									});
								}
							})

						}
					}


				} else {
					uni.showToast({
						title: '请输入' + this.form_data[i].title,
						duration: 2000,
						icon: 'none',
					});
					return false
				}
			}
		},

		formReset(e) {
			console.log(e, "清空")
		},
	}
}
